.App {
    text-align: center;
    color: lightsalmon;
}

.fieldRequired {
    color: red;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: bold;
    vertical-align: text-top;
}

.card {
    width: 40rem;
    padding: 5rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
}

.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.nav-img {
    max-width: 18rem;
}

.table>:not(:first-child) {
    border-top: 2px solid lightseagreen;
}

.no-top-border {
    border-top: none;
}


