#capacityAgeGroup3to6 {
  text-align: center;
} */

.swal-button--confirm {
    background-color: #007bff;
}


/* Neveikia font-size: em */

.swal-text {
    font-size: 16px;
    color: #000;
    text-align: center;
}

.form-control {
    font-size: 0.875rem;
}

.form-group,
.row {
    font-size: 0.875rem;
}

.table {
    font-size: 0.875rem;
}

.pb-3,
.py-3 {
    font-size: 1rem;
}

p {
    font-size: 0.875rem;
}

.navbar-light.navbar-nav.nav-link.active {
    font-size: 1rem;
}

.deleteColumn {
    width: 11%;
}

.formHeader {
    padding-top: 4%;
}

.child--button {
    margin-top: 26px;
    line-height: 22px;
    float: right;
    width: 100%;
}

.modal-bigger-font {
    font-size: 25px;
    margin-left: 30px;
    margin-top: 12px;
    margin-bottom: 5px;
}

.modal-regular-font {
    font-size: 16px;
}

.modal-margin-regular {
    margin-left: 30px;
}

.modal-link {
    font-size: 16px;
    font-style: italic;
    text-decoration: none;
    color: darkblue;
}

.modal-button {
    margin-top: 10px;
    margin-right: 30px;
    margin-bottom: 5px;
    padding: 3px 40px;
    float: right;
}

.searchNotFound {
    color: red;
}

.map-elderate {
    margin-top: 38px;
}

.map--top {
    text-align: center;
}

.dropdown-button {
    color: dimgray;
    font-size: 16px;
    margin-top: 3px;
}